<template>
  <div class="container">
    <div class="text-end mt-4">
      <button class="btn btn-primary" @click="openModal('new')">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">
            
          </th>
          <th width="120">
            
          </th>
          <th></th>
          <th width="120">
            
          </th>
          <th width="120" class="price">
            
          </th>
          <th width="100">
            
          </th>
          <th width="120">
            
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td><img :src="item.imageUrl" class="img-fluid" /></td>
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-end">
            {{ item.origin_price }}
          </td>
          <td class="text-end">
            {{ item.price }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-outline-primary btn-sm"
                @click="openModal('edit', item)"
              >
                
              </button>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm"
                @click="openModal('delete', item)"
              >
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
  <div class="d-flex justify-content-center mt-5">
    <pagination :page="pagination" @get-product="getProducts"></pagination>
  </div>
  <user-product-modal
    :product="tempProduct"
    @update-product="updateProduct"
    ref="userProductModal"
  >
  </user-product-modal>
  <del-product-modal
    :temp-product="tempProduct"
    @del-product="delProduct"
    ref="delProductModal"
  >
  </del-product-modal>
</template>

<script>
// :  Modle 
import userProductModal from '@/components/UserProductModal.vue'
// :  Modle 
import delProductModal from '@/components/DelProductModal.vue'
//   
import pagination from '@/components/Pagination.vue'
//  SweetAlert 
import swal from 'sweetalert'

export default {
  data () {
    return {
      //  function return
      products: [],
      isNew: false, // 
      tempProduct: {
        // :
        imagesUrl: [] // 
      },
      pagination: {},
      page: '',
      userProductModal: {}
    }
  },
  components: {
    userProductModal,
    pagination,
    delProductModal
  },
  mounted () {
    // DOM
    this.getProducts()
  },
  methods: {
    getProducts (page = 1) {
      // 
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products?page=${page}`
      this.$http
        .get(url)
        .then(response => {
          if (response.data.success) {
            this.products = response.data.products
            this.pagination = response.data.pagination
            // console.log(this.products)
          } else {
            console.log(response.data.message)
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    // 
    openModal (isNew, item) {
      if (isNew === 'new') {
        // new
        // 
        this.tempProduct = {
          imagesUrl: []
        }
        this.isNew = true
        this.$refs.userProductModal.openModal()
      } else if (isNew === 'edit') {
        // edit
        this.tempProduct = { ...item }
        this.isNew = false
        this.$refs.userProductModal.openModal()
      } else if (isNew === 'delete') {
        // delete
        this.tempProduct = { ...item }
        this.$refs.delProductModal.openModal()
      }
    },
    // 
    createImages () {
      this.tempProduct.imagesUrl = []
      this.tempProduct.imagesUrl.push('')
    },
    // 
    updateProduct (tempProduct) {
      let url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product`
      let method = 'post' // post 
      if (!this.isNew) {
        url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${tempProduct.id}`
        method = 'put' // put 
      }
      this.$http[method](url, { data: tempProduct })
        .then(response => {
          if (response.data.success) {
            swal('!', '', 'success')
            this.$refs.userProductModal.hideModal()
            this.getProducts()
          } else {
            // alert(response.data.message);
            const errorMsg = response.data.message
            swal('!', `${errorMsg}`, 'error')
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    // 
    delProduct (tempProduct) {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${tempProduct.id}`
      this.$http
        .delete(url)
        .then(response => {
          if (response.data.success) {
            swal('!', '', 'success')
            this.$refs.delProductModal.hideModal()
            this.getProducts()
          } else {
            console.log(response.data.message)
          }
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>
